.vaccine-app-work {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f8f8f8;
  padding-top: 0.9rem;
  padding-bottom: 1rem;
  box-sizing: border-box;
  overflow-y: auto;
}

.vaccine-app-work .vaccine-app-work-baby-index {
  /*position: relative;*/
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box {
  padding: 0.3rem 0;
  border-bottom: 0.01rem solid #e5e5e5;
  font-size: 0.3rem;
  line-height: 0.5rem;
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box .img {
  display: inline-block;
  vertical-align: top;
  width: 0.9rem;
  height: 0.91rem;
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box .title {
  display: inline-block;
  vertical-align: top;
  width: auto;
  height: 0.91rem;
  padding-left: 0.15rem;
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box .title .name {
  font-size: 0.36rem;
  font-weight: 600;
  line-height: 0.5rem;
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box .title .subTitle {
  font-size: 0.3rem;
  line-height: 0.4rem;
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box a {
  display: inline-block;
  vertical-align: middle;
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box a .icon-edit {
  vertical-align: top;
  margin-left: 0.3rem;
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box .date {
  font-size: 0.36rem;
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box.padding-top-0 {
  padding-top: 0;
}

.vaccine-app-work .vaccine-app-work-baby-index .line-box.no-border {
  border: 0;
}

.vaccine-app-work .vaccine-app-work-baby-index .empty {
  position: absolute;
  top: 50%;
  margin-top: -1.25rem;
  box-sizing: border-box;
  width: 100%;
  height: 2.5rem;
  background: url(../../static/media/empty.c7f39d98.png) top center no-repeat;
  background-size: 1.37rem 1.28rem;
  padding-top: 1.7rem;
  font-size: 0.38rem;
  color: #06cc7e;
  text-align: center;
}

.vaccine-app-work .vaccine-app-work-baby-index .empty a {
  display: inline-block;
  padding-left: 0.6rem;
  height: 0.8rem;
  line-height: 0.8rem;
  background: url(data:image/png;base64,) left center no-repeat;
  background-size: 0.43rem 0.42rem;
}

.vaccine-app-work .vaccine-app-work-baby-index .appoint {
  color: #ff7043;
  margin-top: 0.2rem;
}

.vaccine-app-work .vaccine-app-work-baby-index .appoint > span {
  display: inline-block;
  width: 50%;
}

.vaccine-app-work .vaccine-app-work-mom-list .swiper-container {
  position: relative;
  width: 7.5rem;
  height: 3rem;
}

.vaccine-app-work .vaccine-app-work-mom-list .swiper-container img {
  position: relative;
  width: 7.5rem;
  height: 3rem;
}

.vaccine-app-work .vaccine-app-work-mom-list .zone {
  line-height: 0.6rem;
  font-size: 0.3rem;
}

.vaccine-app-work .vaccine-app-work-mom-list .zone .title {
  color: #333;
}

.vaccine-app-work .vaccine-app-work-mom-list .zone .subTitle {
  color: #999;
}

.vaccine-app-work .vaccine-app-work-mom-list .zone .more {
  color: #06cc7e;
  position: absolute;
  bottom: 0.3rem;
  right: 0.4rem;
  width: 0.6rem;
  height: 0.6rem;
}

.vaccine-app-work .vaccine-app-work-appoint .text {
  font-size: 0.32rem;
  color: #333;
  height: 0.9rem;
  line-height: 0.9rem;
}

.vaccine-app-work .vaccine-app-work-appoint .text.line-box {
  border-bottom: 0.01rem solid #e5e5e5;
}

.vaccine-app-work .vaccine-app-work-appoint .text a {
  display: inline-block;
  float: right;
}

.vaccine-app-work .vaccine-app-work-appoint .text a .icon-edit {
  vertical-align: middle;
}

.vaccine-app-work .vaccine-app-work-appoint .appoint-list {
  font-size: 0.3rem;
  color: #666;
  width: 7.2rem;
  margin: 0.1rem 0 0.75rem -0.2rem;
  height: auto;
  box-sizing: border-box;
  text-align: left;
}

.vaccine-app-work .vaccine-app-work-appoint .appoint-list > li {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 2rem;
  height: 1.2rem;
  box-sizing: border-box;
  border: 0.01rem solid #e5e5e5;
  border-radius: 0.1rem;
  background-color: #dff4ec;
  text-align: center;
  padding: 0.1rem 0;
  margin: 0.15rem 0.2rem;
}

.vaccine-app-work .vaccine-app-work-appoint .appoint-list > li > span {
  display: inline-block;
  width: 100%;
  line-height: 0.48rem;
}

.vaccine-app-work .vaccine-app-work-appoint .appoint-list > li.select {
  color: #FFF;
  background-color: #06cc7e;
  border-color: #06cc7e;
}

.vaccine-app-work .vaccine-app-work-appoint .btn-submit {
  width: 2.4rem;
  font-size: 0.36rem;
}